import React, { useEffect, useState } from 'react'
import { Card, Container, Row, Table } from 'react-bootstrap'
import axios from 'axios'
import { message } from 'antd'

const ApprovePCoupon = () => {
  const [couponRequests, setCouponRequests] = useState([])

  const formattedDate = (date) => {
    const dateObject = new Date(date)
    const format = `${dateObject.getFullYear()}-${String(dateObject.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(dateObject.getDate()).padStart(2, '0')} ${String(dateObject.getHours()).padStart(
      2,
      '0',
    )}:${String(dateObject.getMinutes()).padStart(2, '0')}:${String(
      dateObject.getSeconds(),
    ).padStart(2, '0')}`

    return format
  }
  const getCouponRequests = async () => {
    try {
      const res = axios.get('api/v1/masterAdmin/getCouponRequests')
      if ((await res).data.success) {
        setCouponRequests((await res).data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    getCouponRequests()
  }, [])

  return (
    <>
      <Container fluid>
        <h3>ApprovePCoupon</h3>
        <Row>
          <Card>
            <Card.Header>Purchase Coupon Requests</Card.Header>
            <Card.Body>
              <Table responsive>
                <thead>
                  <tr>
                    <th className="text-center">Sn</th>
                    <th className="text-center">Date</th>
                    <th className="text-center">Requested By</th>
                    <th className="text-center">Unique ID</th>
                    <th className="text-center">Quantity</th>
                    <th className="text-center">Single Coupon Price</th>
                    <th className="text-center">Total</th>
                    <th colSpan={2} className="text-center">
                      Action
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {couponRequests && couponRequests.length > 0 ? (
                    couponRequests.map((couponRequest, index) => {
                      return (
                        <tr key={index}>
                          <td className="text-center">{index + 1}</td>
                          <td className="text-center">
                            {formattedDate(couponRequest.purchaseDate)}
                          </td>
                          <td className="text-center">{couponRequest.retailerId.name}</td>
                          <td className="text-center">{couponRequest.retailerId.uniqueId}</td>
                          <td className="text-center">{couponRequest.quantity}</td>
                          <td className="text-center">{couponRequest.retailerId.couponPrice}</td>
                          <td className="text-center">{couponRequest.totalPrice}</td>
                          <td className="text-center">
                            <button
                              className="btn btn-success me-3"
                              onClick={async () => {
                                try {

                                  // userId, singleCouponPrice, quantity
                                  const data = {
                                    uniqueId: couponRequest.retailerId.uniqueId,
                                    singleCouponPrice: couponRequest.retailerId.couponPrice,
                                    quantity: couponRequest.quantity,
                                    coupon_id : couponRequest._id
                                  }
                                  const res = await axios.post(
                                    'api/v1/test/calculateAndAssignCommissions',
                                    data,
                                  )
                                  if (res.data.success) {
                                    message.success('Coupon Aprrove successfully')
                                    getCouponRequests()
                                  }
                                } catch (error) {
                                  console.log(error)
                                }
                              }}
                            >
                              Approve
                            </button>
                            <button
                              className="btn btn-danger"
                              onClick={async () => {
                                try {
                                  const data = { purchaseId: couponRequest._id }
                                  const res = await axios.post(
                                    'api/v1/masterAdmin/rejectCouponPurchase',
                                    data,
                                  )
                                  if (res.data.success) {
                                    getCouponRequests()
                                  }
                                } catch (error) {
                                  console.log(error)
                                }
                              }}
                            >
                              Reject
                            </button>
                          </td>
                        </tr>
                      )
                    })
                  ) : (
                    <tr>
                      <td colSpan="8" className="text-center text-danger">
                        No Coupon Requests
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Row>
      </Container>
    </>
  )
}

export default ApprovePCoupon
